import React from 'react';
import { useFormContext, useWatch } from 'react-hook-form';
import styled from 'styled-components';

import { Flex } from '@/components/ui/StyledComponents.tsx';
import { CustomInput } from '@/components/ui/CustomInput/CustomInput.tsx';
import { CustomTextarea } from '@/components/ui/CustomTextarea/CustomTextarea.tsx';
import { STEPS_CONFIG } from '@/components/DoctorAppointmentMake/DistrictBooking/steps-config.tsx';
import {
  CustomSelect,
  SelectOption,
  SelectValue,
} from '@/components/ui/CustomSelect/CustomSelect.tsx';
type GenderOption = SelectOption & { value: 'm' | 'f' };

const PageTitle = styled.span`
  width: 100%;
  font-size: ${props => props.theme.typography.fontSize.md};
  font-weight: ${props => props.theme.typography.fontWeight.semibold};
  color: ${props => props.theme.colors.black};
  text-align: left;
`;

const genderOptions: GenderOption[] = [
  { label: '', value: 'm' },
  { label: '', value: 'f' },
];

export const Step5: React.FC = () => {
  const { register, setValue, control } = useFormContext();
  const stepFields = STEPS_CONFIG[4].fields;
  const [lastName, firstName, birthdate, snils, policeNumber, phoneField, mail] = stepFields;

  const phoneValue = useWatch({
    control,
    name: phoneField,
  });

  const genderValue = useWatch({
    control,
    name: 'gender',
  });

  const handlePhoneChange = (value: string) => {
    let formattedValue = value.replace(/\D/g, '');

    if (formattedValue.length > 0) {
      formattedValue = formattedValue.substring(0, 11);
      let result = '+7';

      if (formattedValue.length > 1) {
        result += ` (${formattedValue.substring(1, 4)}`;
      }
      if (formattedValue.length >= 5) {
        result += `) ${formattedValue.substring(4, 7)}`;
      }
      if (formattedValue.length >= 8) {
        result += `-${formattedValue.substring(7, 9)}`;
      }
      if (formattedValue.length >= 10) {
        result += `-${formattedValue.substring(9, 11)}`;
      }

      setValue(phoneField, result, { shouldValidate: true });
    } else {
      setValue(phoneField, '', { shouldValidate: true });
    }
  };
  const handleChangeGender = (value: SelectValue | null) => {
    setValue('gender', value, { shouldValidate: true });
  };

  const phonePattern = {
    value: /^\+7\s\(\d{3}\)\s\d{3}-\d{2}-\d{2}$/,
    message: '    +7 (999) 999-99-99',
  };

  return (
    <Flex $direction={'column'} $gap={16}>
      {/*Title*/}
      <PageTitle> </PageTitle>
      {/*    */}
      <CustomInput
        title=""
        placeholder=""
        required
        showErrorText
        register={register(lastName, {
          required: ' ',
          minLength: {
            value: 2,
            message: '    2 ',
          },
        })}
      />

      <CustomInput
        title=""
        placeholder=""
        required
        showErrorText
        register={register(firstName, {
          required: ' ',
          minLength: {
            value: 2,
            message: '    2 ',
          },
        })}
      />

      <CustomInput title="" placeholder="" register={register('middleName')} />

      <CustomInput
        title=" "
        required
        type="date"
        showErrorText
        register={register(birthdate, {
          required: '  ',
          validate: value => {
            if (!value) return true;
            const birthDate = new Date(value);
            const today = new Date();
            const age = today.getFullYear() - birthDate.getFullYear();
            return (age >= 0 && age <= 120) || '   ';
          },
        })}
      />

      <CustomSelect
        title=""
        options={genderOptions}
        value={genderValue}
        onChange={handleChangeGender}
        isClearable={false}
        cyName="gender-select"
      />

      <CustomInput
        title=""
        placeholder=""
        required
        showErrorText
        register={register(snils, {
          required: ' ',
          minLength: {
            value: 2,
            message: '    2 ',
          },
        })}
      />
      <CustomInput
        title="  "
        placeholder=""
        required
        showErrorText
        register={register(policeNumber, {
          required: '   ',
          minLength: {
            value: 2,
            message: '      2 ',
          },
        })}
      />
      <CustomInput title="  " register={register('policeSeria')} />
      {/*   */}
      <CustomInput
        title=""
        required
        placeholder="+7 (999) 999-99-99"
        value={phoneValue || ''}
        onChange={handlePhoneChange}
        register={register(phoneField, {
          required: '  ',
          pattern: phonePattern,
          validate: value => {
            const numbers = value?.replace(/\D/g, '') || '';
            return numbers.length === 11 || '   11 ';
          },
        })}
        showErrorText
      />

      <CustomInput
        title=" "
        type="email"
        placeholder="example@mail.ru"
        required
        showErrorText
        register={register(mail, {
          required: ' email',
          pattern: {
            value: /^\S+@\S+$/i,
            message: '  email',
          },
        })}
      />

      {/*  */}
      <CustomTextarea
        title=""
        placeholder=" ..."
        rows={3}
        register={register('comments')}
      />
    </Flex>
  );
};
